/**
* @type {string}
* @description 
* @description development 
* @description test 
* @description release 
* @description production 
*/
export const env = process.env.NODE_ENV;

/**
* @type {string}
* @description  Tab 
*/
export const title = "Vui Design Pro";

/**
* @type {boolean} true | false
* @description  false
*/
export const useI18n = false;

/**
* @type {string}
* @description  path  name 
*/
export const rootRoute = "/";

/**
* @type {array}
* @description  path  name 
*/
export const whiteList = ["/login"];

/**
* @type {object}
* @description 
*/
const apiPaths = {
	development: "//www.easy-mock.com/mock/5dd110849a4da91d9ad9446c/vui",
	test: "//www.test.com/api",
	release: "//www.release.com/api",
	production: "//www.production.com/api"
};

export const baseURL = apiPaths[env];

/**
* @type {number}
* @description token  cookie  1 
*/
export const cookieExpires = 1;

/**
* @type {array}
* @description 
*/
export const colors = ["#2d8cf0", "#faad14", "#ed5565", "#52c41a", "#b381f1", "#60baf7", "#e6da46", "#f98a9c", "#84e0e5", "#dba7df"];

/**
* @description Export all configurations by default
*/
export default {
	env,
	title,
	useI18n,
	rootRoute,
	whiteList,
	baseURL,
	cookieExpires,
	colors
};


